html {
  --black: #000000;
  --white: #e6dfd1; // Farrow & Ball Schoolhouse White
}

body {
  background-color: var(--white);
  font-family: arial, sans-serif;
  margin: 0;
}

a {
  border-bottom: solid 1px var(--black);
  color: inherit;
  text-decoration: none;

  &:hover {
    border-bottom: none;
  }

  &:visited {
    color: inherit;
  }
}

img {
  max-width: 100%;
}
